import React, { Component } from 'react';

export class Block extends Component {
    constructor() {
        super();
        this.state = {
            gridStyle: null,
            updateHandle: null
        }
        this.Update = this.Update.bind(this);
    }

    componentDidMount() {
        this.setState({
            gridStyle: this.GenerateGridStyle(document.documentElement.clientWidth, document.documentElement.clientHeight),
            updateHandle: setInterval(this.Update, 16)
        });
    }

    componentWillUnmount() {
        if(this.state.updateHandle !== null) {
            clearInterval(this.state.updateHandle);
        }
    }

    Update() {
        this.setState({
            gridStyle: this.GenerateGridStyle(document.documentElement.clientWidth, document.documentElement.clientHeight)
        });
    }

    GenerateGridStyle(width, height) {
        return {
            backgroundColor: this.props.backgroundColor ? this.props.backgroundColor : "rgba(0,0,0,0)",
            color: this.props.fontColor ? this.props.fontColor : "#000000",
            width: (width / 12 * (this.props.columns ? this.props.columns : 12)).toString() + "px",
            height: (height / 12 * (this.props.rows ? this.props.rows : 12)).toString() + "px"
        }
    }

    render() {
        return (
            <div style={this.state.gridStyle}>
                {this.props.children}
            </div>
        )
    }
}
