import React, { Component, Fragment } from 'react';
import { Block } from './react-volt';
import Logo64 from './img/Logo64.png';

export default class App extends Component {
  GenerateHeadingStyle() {
    return {
      textAlign: "center",
      verticalAlign: "middle",
      fontSize: "1.2em",
      fontWeight: "700",
      lineHeight: "90vh"
    }
  }

  GenerateLogoStyle() {
    return {
      display: "inline-block",
      verticalAlign: "middle",
      marginLeft: "0.5em"
    }
  }

  GenerateNameStyle() {
    return {
      display: "inline-block",
      verticalAlign: "middle",
      marginLeft: "0.25em",
      marginRight: "0.5em"
    }
  }

  GenerateButtonStyle(fontColor = "#000000", backgroundColor = "rgba(0,0,0,0)") {
    return {
      border: "none",
      minWidth: "8em",
      height: "100%",
      color: fontColor,
      backgroundColor: backgroundColor,
      textAlign: "center",
      verticalAlign: "middle",
      fontSize: "1em",
      fontWeight: "700",
      letterSpacing: "0.07em",
      cursor: "pointer"
    }
  }

  render() {
    switch(true) {
      case true: return (
        <div>
          <Block backgroundColor="#0c0c0c" columns="12" rows="1">
            <button style={this.GenerateButtonStyle("#ffffff")}>
              <img style={this.GenerateLogoStyle()} src={Logo64} alt="Logo"/>
              <h2 style={this.GenerateNameStyle()}>Volt Games</h2>
            </button>
            <button style={this.GenerateButtonStyle("#ffffff")}>Games</button>
            <button style={this.GenerateButtonStyle("#ffffff")}>Support</button>
          </Block>
          <Block backgroundColor="#000000" fontColor="#ffffff" columns="12" rows="11">
            <p style={this.GenerateHeadingStyle()}>voltgames.com.au is under development, please visit again later.</p>
          </Block>
        </div>
      );
      default: return <Fragment/>;
    }
  }
}
